import React, { useState, useEffect } from "react";
import List from "./List";
import Alert from "./Alert";

function getLocalStorage() {
  let items = localStorage.getItem("items");
  if (items) {
    return JSON.parse(localStorage.getItem("items"));
  } else {
    return [];
  }
}
function App() {
  const [item, setItem] = useState("");
  const [items, setItems] = useState(getLocalStorage());
  const [edit, setEdit] = useState(false);
  const [editId, setEditId] = useState("");
  const [alert, setAlert] = useState({
    type: "",
    show: false,
    message: ""
  });

  useEffect(() => {
    localStorage.setItem("items", JSON.stringify(items));
  }, [items]);

  function handleSubmit(e) {
    e.preventDefault();
    if (!item) {
      handleAlert(true, "Please enter the item", "danger");
    } else if (item && edit) {
      setItems(
        items.map((element, index) => {
          if (index === editId) {
            return item;
          }
          return element;
        })
      );
      setItem("");
      setEdit(false);
      setEditId(null);
      handleAlert(true, "list updated", "success");
    } else {
      setItems((prev) => {
        return [...prev, item];
      });
      setItem("");
      setEdit(false);
      handleAlert(true, "Item added successfully", "success");
    }
    localStorage.getItem(items);
  }

  function handleAlert(show = false, message = "", type = "") {
    setAlert({ show, message, type });
  }

  const handleClear = () => {
    setItems([]);
    handleAlert(true, "All items cleared ", "danger");
  };

  const handleDelete = (id) => {
    let newItem = items.filter((element, index) => index !== id);
    console.log(newItem);
    setItems(newItem);
    handleAlert(true, "Item deleted successfully", "success");
  };

  const handleEdit = (id) => {
    const specificItem = items.find((element, index) => index === id);
    console.log(specificItem);
    setEdit(true);
    setEditId(id);
    setItem(specificItem);
  };

  return (
    <section className="section-center">
      <form className="grocery-form">
        {alert.show && (
          <Alert {...alert} removeAlert={handleAlert} items={items} />
        )}
        <h3>Grocery Bud</h3>
        <div className="form-control">
          <input
            placeholder="e.g eggs"
            value={item}
            type="text"
            className="grocery"
            onChange={(e) => setItem(e.target.value)}
          />
          <button className="submit-btn" type="submit" onClick={handleSubmit}>
            {edit ? "Edit" : "Submit"}
          </button>
        </div>
      </form>
      <div className="grocery-container">
        {items.map((element, index) => {
          return (
            <List
              key={index}
              element={element}
              handleDelete={handleDelete}
              id={index}
              handleEdit={handleEdit}
            />
          );
        })}
        <button onClick={handleClear} className="clear-btn">
          Clear Items
        </button>
      </div>
    </section>
  );
}

export default App;
