import React, { useEffect } from "react";

const Alert = ({ message, type, removeAlert, items }) => {
  useEffect(() => {
    let interval = setTimeout(() => {
      removeAlert();

      return () => clearInterval(interval);
    }, 3000);
  }, [items]);
  return <p className={`alert alert-${type}`}>{message}</p>;
};

export default Alert;
